.navbar {
  display: flex;
  justify-content: space-between;
  align-items: left;
  background-color: #333;
  padding: .75rem;
}

.navbar-brand {
  font-size: 1.5rem;
  color: white;
  text-align: center;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.navbar-links a {
  color: white;
  text-decoration: none;
}
