.iframe-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh; /* Full viewport height */
  overflow: hidden;
}

.centered-iframe {
  width: 62.63%; /* Adjust the width as needed */
  height: 80%; /* Adjust the height as needed */
  border: none; /* Optional: remove iframe border */
}