.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem;
}

.hero-content {
  width: 35%;
  
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: auto;
}

.hero h2 {
  color: #00bcd4;
  font-size: 2rem;
}

.hero p {
  margin: rem 0;
  padding: 10px 0px
}

.hero button {
  padding: 1rem 3rem;
  background-color: #00bcd4;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.hero-image {
  width: 60%;
  padding-top: 2rem;
  padding-right: 2rem;
}

.hero-image img {
  width: 90%;
  height: auto;
}
